<template>
  <div class="person_left">
    <el-upload
      class="avatar-uploader"
      action="http://localhost:8081/upload"
      :data="{
        id: this.user.id,
        username: this.user.username,
      }"
      :show-file-list="false"
      :on-success="handleAvatarSuccess"
      :before-upload="beforeAvatarUpload"
    >
      <img v-if="user.avater" :src="user.avater" class="avatar" />
      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
    </el-upload>
    <el-form
      :label-position="labelPosition"
      label-width="80px"
      :model="this.user"
    >
      <el-form-item label="账号">
        <el-input v-model="user.username" disabled></el-input>
      </el-form-item>
      <el-form-item label="昵称">
        <el-input v-model="user.nickname" disabled></el-input>
      </el-form-item>
      <el-form-item label="手机">
        <el-input v-model="user.phone" disabled></el-input>
      </el-form-item>
      <el-form-item label="邮箱">
        <el-input v-model="user.email" disabled></el-input>
      </el-form-item>
      <el-form-item style="margin: 50px 0">
        <el-button
          type="primary"
          @click="show"
          style="margin-left: -40px; width: 80%"
          >修改</el-button
        >
      </el-form-item>
    </el-form>
    <el-drawer
      title="个人信息"
      :visible.sync="drawer"
      :with-header="false"
      @blur="this.drawer = false"
    >
      <ChangeInfo></ChangeInfo>
    </el-drawer>
  </div>
</template>

<script>
import ChangeInfo from "../../../Admin/ChangeInfo.vue";
export default {
  name: "Personleft",

  data() {
    return {
      user: this.$store.getters.getUser,
      labelPosition: "right",
      drawer: false,
    };
  },

  mounted() {},

  methods: {
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    show() {
      this.drawer = true;
    },
    handleAvatarSuccess(res, file) {
      this.user.avater = URL.createObjectURL(file.raw);
    },
  },
  components: {
    ChangeInfo,
  },
};
</script>

<style lang="less" scoped>
.person_left {
  position: relative;
  margin-top: 40px;
  width: 30%;
  height: 75vh;
  background-color: #ffffff;
  border-radius: 20px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  position: absolute;
  left: 37%;
  top: -8%;
  width: 100px;
  height: 100px;
  display: block;
  border-radius: 50%;
  box-shadow: 0px 0px 15px rgba(255, 255, 170, 0.886);
}
.el-form {
  margin-top: 100px;
  width: 90%;
}
</style>