<template>
  <div class="person_right">
    <h3>资质认证<i class="el-icon-caret-right"></i></h3>
    <div class="tip">
      <i class="el-icon-magic-stick" style="margin: 0 10px"></i
      ><span style="margin: 0 10px"
        ><p>
          &nbsp;&nbsp;&nbsp;广告主认证信息为必填项，请上传真实的认证资料，如果审核通过后需要修改信息，请填写新的资质认证信息，提交审核，以最新审核的认证信息为准。
        </p>
      </span>
    </div>
    <div style="padding: 0 20px; width: 400px">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="id" prop="id" v-if="false">
          <el-input v-model="form.id"></el-input>
        </el-form-item>
        <el-form-item label="username" prop="username" v-if="false">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="真实姓名" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="公司名称" prop="corporateName">
          <el-input v-model="form.corporateName"></el-input>
        </el-form-item>
        <el-form-item label="身份证号" prop="IDNumber">
          <el-input v-model="form.IDNumber"></el-input>
        </el-form-item>
        <el-form-item label="营业执照" prop="license">
          <el-upload
            ref="MyUpload"
            action="http://localhost:8081/license"
            list-type="picture-card"
            :auto-upload="false"
            :data="this.user"
          >
            <i slot="default" class="el-icon-plus"></i>
            <div slot="file" slot-scope="{ file }">
              <img
                class="el-upload-list__item-thumbnail"
                :src="file.url"
                alt=""
              />
              <span class="el-upload-list__item-actions">
                <span
                  v-if="!disabled"
                  class="el-upload-list__item-delete"
                  @click="handleRemove(file)"
                >
                  <i class="el-icon-delete"></i>
                </span>
              </span>
            </div>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="" />
          </el-dialog>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">提交</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Personright",

  data() {
    return {
      user: this.$store.getters.getUser,
      form: {
        id: this.$store.getters.getUser.id,
        username: this.$store.getters.getUser.username,
      },
      dialogImageUrl: "",
      dialogVisible: false,
      disabled: false,
      rules: {
        name: [
          { required: true, message: "请输入真实姓名", trigger: "blur" },
          {
            min: 2,
            max: 10,
            message: "长度在 2 到 10 个字符",
            trigger: "blur",
          },
        ],
        corporateName: [
          { required: true, message: "请输入公司名称", trigger: "blur" },
          {
            min: 1,
            max: 20,
            message: "长度在 1 到 20 个字符",
            trigger: "blur",
          },
        ],
        IDnumber: [
          { required: true, message: "请输入身份证号", trigger: "blur" },
        ],
      },
    };
  },

  mounted() {},

  methods: {
    onSubmit() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          this.$axios.post("/UserInfoDetail", this.form).then((res) => {
            if (res.data.code == 200) {
              this.$message({
                message: "提交成功",
                type: "success",
              });
            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
          this.$refs["MyUpload"].submit();
          this.$refs["MyUpload"].clearFiles();
        } else {
          this.$message.error("请填写完整信息");
          return false;
        }
      });
    },
    handleRemove(file) {
      // 清空文件
      this.$refs["MyUpload"].clearFiles();
    },
    reset() {
      this.$refs["form"].resetFields();
      this.handleRemove();
    },
  },
};
</script>

<style lang="less" scoped>
.person_right {
  width: 67%;
  height: 82vh;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  h3 {
    float: left;
    margin: 15px 0 20px 30px;
    font-size: 16px;
    font-weight: 400;
  }
  .tip {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 50px 20px 20px 20px;
    height: 80px;
    width: 95%;
    border-radius: 10px;
    background-color: #f5f5f5;
    p {
      font-size: 14px;
      font-weight: 400;
      color: #ffad9b;
    }
  }
}
</style>